import { Component, Host, Prop, State, h } from '@stencil/core';
import {Peer} from 'peerjs';

@Component({
  tag: 'call-user',
  styleUrl: 'call-user.css',
  shadow: false,
})
export class CallUser {
  @Prop() userNameProps : any;

  @State() isCallUser: boolean = false;
  @State() isLoading: boolean = true;
  @State() isError: boolean = false;
  @State() isCallConnected: boolean = false;

  assignIT : boolean = true;
  assignvIT: boolean = true;
  callerRef: any;
  peer: any;
  streamObject: any;
  myVideo: any;
  callerVideo: any;
  remoteStreamObject : any;

  callUser() {
    const call = this.peer.call(this.callerRef.value, this.streamObject);
		call.on("stream", (streamObject: any) => {
      console.log("stream recieved after calling you", streamObject,this.callerVideo);
			this.remoteStreamObject = streamObject; 
      this.componentDidRender();
		});
  }

  async getUserMedia() {
    try {
      this.isLoading = true;
      this.isError = false;
      this.streamObject = await window.navigator.mediaDevices.getUserMedia(
        { video: true, audio: true },
      );
    } catch {
      this.isError = true;
    } finally {
      this.isLoading = false;
    }
  }
  componentWillLoad(){
    this.getUserMedia();
    this.peer = new Peer(this.userNameProps);
    this.peer.on('open', () => this.isLoading = false);
    this.peer.on("call", (call: any) => {
      call.answer(this.streamObject); // Answer the call with an A/V stream.
      call.on("stream", (remoteStream: any) => {
        console.log('reciever side stream object is', remoteStream);
        this.remoteStreamObject = remoteStream;
        this.componentDidRender();
      });
    });
  }
  componentDidRender() {
    if (this.assignIT && this.streamObject) {
      this.myVideo.srcObject = this.streamObject;
      this.assignIT = false;
    }
    if (this.assignvIT && this.remoteStreamObject) {
      this.callerVideo.srcObject = this.remoteStreamObject;
      this.callerVideo.style.display = "block";
      this.callerVideo.style.background = "blue";
      this.myVideo.style.height = "20vh";
      this.myVideo.style.width = "18vw";
      this.myVideo.style.zIndex = 1;
      this.myVideo.style.bottom = "10vh";
      this.myVideo.style.background = "red";
      this.myVideo.style.right = "20px";
      this.myVideo.style.border = "2px solid lightcyan";
      this.assignvIT = false;
    }
  }
  
  render() {
    if (this.isLoading) {
      return <div>Loading please wait</div>;
    } else if (this.isError) {
      return <div>There was some error while getting the camera details</div>
    }
    return (
      <Host>
        connection Established your username is {this.userNameProps}
        <div>{this.userNameProps}</div>
        {
          this.isCallConnected == false ? 
            this.isCallUser ? <div>
              <input type='text' ref={e => this.callerRef = e}/>
              <button onClick={() => this.callUser()}>Connect</button>
            </div> : 
            <div>
              <button onClick={() => this.isCallUser = true}>Connect with other</button>
            </div> : ""
        }
        <div class="flyer-video-container">
          <video class="flyer-host-video" ref={e => this.myVideo = e} autoPlay controls muted width={510} height={510}></video>
          <video class="flyer-reciever-video" ref={e => this.callerVideo = e} autoplay controls muted width={510} height={510}></video>
        </div>
      </Host>
    );
  }

}
