:host {
  display: block;
}

.flyer-video-container {
  display: flex;
  height: 100vh;
  width:100%;
  flex-wrap: wrap;
  position: relative;
}

.flyer-host-video, .flyer-reciever-video {
  width: 100%;
  min-width: 10vw;
  height: 100%;
  min-height: 10vh;
  position: absolute;
  background: gray;
  border-radius: 1vh;
  }

.flyer-reciever-video {
  position: absolute;
  display: none;
}